* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

header {
  display: flex;
  height: 150px;
  background-color: #69ee;
  border-bottom: #58dd 1px solid;
}

header img {
  display: block;
  height: 150px;
}

header span {
  margin-left: 20px;
  line-height: 150px;
  font-family: "Bellota", cursive;
  font-weight: 700;
  font-style: italic;
  text-align: center;
  color: white;
  font-size: 40px;
}

.App {
  text-align: center;
}
